import titles from '../json/titles.json'
import Sites from '../json/websites.json'
import axios from 'axios'

const getTitle = () => {
    const titleLen = titles.length;
    const index = Math.floor(Math.random() * titleLen);
    return titles[index]
};

const getCatagory = (value) => {
    return Sites[value];
};

const goTo = (site) => {
    window.location.href = "//" + site
}

const getNews = () => {
    const info = {url: "https://newsapi.org/v2/top-headlines?",
                  source: "sources=bbc-news,the-verge",
                  api: "&apiKey=8be83b89c4064d959f4d4d11ad045f5e"}
    return info.url+info.source+info.api;
  };


export { getTitle, getCatagory, goTo, getNews } 