import React from 'react';
import styled from 'styled-components'
import { getNews } from '../../components/functions';

const NewsContainerStyled = styled.div`
margin: 2vh;
width:fill-available;
height:70vh;
overflow:auto;
font-family: ${props => props.theme.fontFamily};`

const NewsHeader = styled.div`
width:fill-available;
height:auto;
display:block;`

const NewsCredits = styled.div`
display: flex;`

const NewsTitle = styled.a`
display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;`

const NewsImage = styled.img`
width:100%;
height:auto;`

const NewsArticle = styled.div`
border:3px solid ${props => props.theme.seperator};
border-radius: 20px;
margin-bottom: 2vh;
padding: 1vh;`

export default class NewsContainer extends React.Component {

    constructor() {
        super();
        this.state = { data: [] };
      }
    
      async componentDidMount() {
        try {
          const response = await fetch(getNews());
          const json = await response.json();
          this.setState({ data: json.articles });
        } catch (error) {
          console.log(error);
        }
      }

      render() {
          return (
          <NewsContainerStyled>
            {this.state.data.map((article => {
              return (
                <NewsArticle>
                <NewsHeader>
                <NewsTitle href={article.url}>{article.title}</NewsTitle>
                <NewsCredits>
                <p>{article.author}</p>
                <p>{article.source.name}</p>
                </NewsCredits>
                </NewsHeader>
                <NewsImage src={article.urlToImage}/>
                <p>{article.content}</p>
                </NewsArticle>
              )
            }))}
            </NewsContainerStyled>)
      }
}